import * as React from 'react';

import { SEO } from 'components/SEO';
import { JobEntry } from 'components/JobEntry';

import './Workpanel.css'

export interface WorkPanelProperties {
  renderCallback: () => void;
}

export class WorkPanel extends React.Component<WorkPanelProperties, {}> {
  public constructor(props) {
    super(props);
    this.props.renderCallback();
  }
  public render() {
    return (
      <>
        <SEO url="professional" />
        <div className="workpanels">
          <div className="entries">
            <JobEntry company="KPMG" role="Software Engineering Intern" time="Summer 2019">
              <p>
                As a member of the Lighthouse team, I got the opportunity to work on a multitude of projects related to NLP.
                I developed a Sentiment modelling pipeline to monitor reputational risk with Twitter data, which
                allows users to gain actionable insight on companies through social media. I also built a hawkish/dovish sentiment 
                classifier to accurately classify financial documents. Holistically, I matured my understanding of datastore management, 
                data wrangling, and software architecture.
              </p>
            </JobEntry>
            <JobEntry company="Interset" role="Software Engineering Intern" time="Fall 2018">
              <p>
                At Interset, I spent most of my time on the Automation team. I helped build out 
                an Automated Install Framework, which allowed users to install the product through
                a CLI. This project saved over 1000 man-hours in manual installations. This internship challenged me to
                learn completely new languages and frameworks, all while having to produce code on a timely basis. As a result, 
                I learned how to assess problems effectively, be resourceful, and how to Google well.
              </p>
            </JobEntry>
            <JobEntry company="HelpWear" role="Software Engineering Intern" time="Winter 2018">
              <p>
                As part of a small software developement team, I built a mobile app to display  stream and display live voltage readings from electrical signal 
                sensors over bluetooth. I also designed and implemented a classification algorithm able to detect patient ECG (electrocardiogram) features 
                with 90% accuracy. As my first real-world experience of the software world, this sparked my interest in the tech world and inspired me 
                to further develop my technical skills outside of school! 
                </p>
            </JobEntry>
            <div className="job-entry">
              <h3>2017</h3>
              <div className="workpanel-marker workpanel-marker-tech">
              </div>
              <div className="timeline-entry">
                <div className="timeline-entry-title">
                  <h4><b>University of Waterloo</b> Started University! <small>Fall 2017</small></h4>
                </div>
              </div>
            </div>
            <div className="job-entry">
              <div className="workpanel-marker">
              </div>
            </div>
          </div>
          <div className="workpanels-content">
          </div>
        </div>
      </>
    );
  }
}



